    import headTop from '@/components/base/header/head'
    export default {
        components: {
            headTop
        },
        data() {
            return {
                tabbarList: [
                    { text:"服务排行",to:"/statistics/serveRanking" },
                    { text:"实时监测",to:"/statistics/monitor" },
                    { text:"工时利用率",to:"/statistics/useRatio" }
                ],
                info: '',  //  初始数据
                infoTwo: '',  //  初始数据
                infoThere: '',  //  初始数据
                index: 0,
                demo2: '精选',
                servedata: 0,  // 默认显示服务效率
                value: 0,    // 默认选中今天
                show: false,   // 默认自定义弹窗隐藏
                startTime: null,  //  开始时间
                dataTime: null,   //  筛选时间
//                服务监测
                staffList: ["接单数量","超时单数","超时分钟"],
                // 服务类型列表
                typeList: [
                    { sales: 38, year: '点单' },
                    { sales: 52, year: '换麦' },
                    { sales: 50, year: '打扫' },
                    { sales: 195, year: '杯子' },
                    { sales: 48, year: '开水' },
                    { sales: 38, year: '其他'}
                ],
            }
        },
        created(){
            this.dataTime = this.common.commonFun.get_date(0)
        },
        mounted(){
            this.getInfo();  //  初始化数据
            // this.oneRender();
        },
        methods:{
            // oneRender() {
            //     this.$nextTick(() => {
            //         chart.tooltip({
            //             showTitle: true, // 展示  tooltip 的标题
            //             layout: 'vertical'
            //         });
            //     });
            // },
            getInfoT(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.getServiceMonitor,
                    type: "post",
                    data: {
                        time: this.dataTime
                    },
                    success: (response, allResponse) => {
                        // console.log('response',response)
                        this.infoTwo = response;
                    }
                });
            },
            getInfoThere(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.getServiceTypeStatistics,
                    type: "post",
                    data: {
                        time: this.dataTime
                    },
                    success: (response, allResponse) => {
                        // console.log('response',response)
                        this.infoThere = response;
                    }
                });
            },
            getInfo(){
                this.common.commonFun.ajax({
                    dom: this,
                    url: this.common.apiUrl.getServiceEfficiency,
                    type: "post",
                    data: {
                        time: this.dataTime
                    },
                    success: (response, allResponse) => {
                        // console.log('response',response.title)
                        this.info = response
                    }
                });
            },
//            头部服务切换
            onItemClick(index){
                this.value = 0;
                this.servedata = index;
                this.startTime = null;
                this.dataTime = this.common.commonFun.get_date(0);
                if(index == 0){
                    this.getInfo();
                }
                if(index == 1){
                    this.getInfoT();
                }
                if(index == 2){
                    this.getInfoThere();
                }
            },
            date(index){
                console.log("我选中了",index);
                this.value = index;
                if(this.servedata == 0){
                    if(index == 0){
                        this.dataTime = this.common.commonFun.get_date(0);
                        this.getInfo();
                    }
                    if(index == 1){
                        this.dataTime = this.common.commonFun.get_date(1);
                        this.getInfo();
                    }
                    if(index == 2){
                        this.show = true;
                    }
                }
                if(this.servedata == 1){
                    if(index == 0){
                        this.dataTime = this.common.commonFun.get_date(0);
                        this.getInfoT();
                    }
                    if(index == 1){
                        this.dataTime = this.common.commonFun.get_date(1);
                        this.getInfoT();
                    }
                    if(index == 2){
                        this.show = true;
                    }
                }
                if(this.servedata == 2){
                    if(index == 0){
                        this.dataTime = this.common.commonFun.get_date(0);
                        this.getInfoThere();
                    }
                    if(index == 1){
                        this.dataTime = this.common.commonFun.get_date(1);
                        this.getInfoThere();
                    }
                    if(index == 2){
                        this.show = true;
                    }
                }
            },
//            自定义弹窗=>点击确定
            onConfirm(){
                console.log('我点击了确定',this.startTime)
                if(this.servedata == 0){
                    this.dataTime = this.startTime;
                    this.getInfo();
                }
                if(this.servedata == 1){
                    this.dataTime = this.startTime;
                    this.getInfoT();
                }
                if(this.servedata == 2){
                    this.dataTime = this.startTime;
                    this.getInfoThere();
                }
            },
//            员工监测
            staff(){
                this.$router.push("/statistics/serveRanking")
            },
//            服务监测更多
            more(){
                this.$router.push("/statistics/serve/servecomplain")
            },
//            服务类型详情
            details(){
                this.$router.push("/statistics/serve/servetype")
            }
        }
    }



